@import '../../styles/mixins.scss';

.modal {
  position: fixed;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 1;

  background: linear-gradient(rgba(163,190,189,0.2) 0%, rgba(63,161,161,1) 47%, rgba(0,30,31,0.3) 100%);

  &__text {
    font-size: 40px;
    margin-bottom: 20px;
    text-shadow: 1px 1px 5px #fff;
  }
  &__content {
    width: 200px;
    height: 200px;
    background-image: url(../../assets/success-image.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }
}

@include for-tabs {
  .modal {
    &__content {
      height: 400px;
      width: 400px;
    }
  }
}