@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.form {
  text-align: center;

  &__title {
    margin-bottom: 50px;

    font-size: 40px;
    font-weight: 400;
    color: $color-title-black;
  }

  &__input-container {
    position: relative;
  }

  &__input {
    box-sizing: border-box;
    width: 100%;

    padding: 16px 14px 16px;
    margin-bottom: 50px;

    border: 1px solid $color-border-gray;
    border-radius: 4px;
    outline: none;

    &--phone {
      &::after {
        position: absolute;
        top: 60px;
        left: 0;
        content: '+38 (XXX) XXX - XX - XX';
        font-size: 12px;
        color: $color-grey-disabled;
      }
    }

    &--error {
      border: 2px solid red;
    }
  }

  &__position{

    &-title {
      margin-bottom: 12px;
    }

    &-container {
      display: flex;
      flex-direction: column;
      text-align: left;
  
      margin-bottom: 50px;
    }

    &-input {
      margin-right: 10px;
    }

    &-label {
      margin-bottom: 10px;
    }
  }

  &__file-container {
    box-sizing: border-box;
    position: relative;
    text-align: left;

    padding: 14px 0;
    margin-bottom: 50px;

    width: 100%;

    border: 1px solid $color-border-gray;
    border-radius: 4px;

    &--error {
      border: 2px solid red;
    }
  }

  &__file {
    box-sizing: border-box;
    padding-left: 16px;
    &::after {
      content: 'Upload your photo';
    }

    &--loaded {
      padding-left: 16px;
      &::after {
        content: '';
      }
    }
  }

  &__file-button {
    box-sizing: border-box;
    padding: 10px;

    color: $color-title-black;
    background-color: $color-white;

    border: 1px solid $color-title-black;
    cursor: pointer;

    border-radius: 4px 0 0 4px;

    &:hover {
      background-color: darkgray;
    }

    &--error {
      box-sizing: border-box;
      border: 0;
      border-right: 2px solid red;
      border-radius: 0;
    }
  }

  &__file-picker {
    display: none;
  }

  &__submit-button {
    min-width: 100px;
    min-height: 34px;

    font-size: 16px;

    color: $color-white;
    background-color: $color-grey-disabled;
    border: none;
    border-radius: 80px;
  }
  &__error {
    position: absolute;
    top: 60px;
    color: red;
  }
}

@include for-tabs {
  .form {
    width: 380px;
    margin: 0 auto;
  }
}