@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.header {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 140px;

  color: $color-white;

  &__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    height: 60px;
    padding: 0 16px;
    
    background-color: $color-white;
  }

  &__btn {
    display: inline-block;

    width: 100px;
    height: 34px;
    line-height: 34px;

    background-color: $color-main;
    color: $color-title-black;
    border: none;
    border-radius: 80px;
    font-size: 16px;
    text-decoration: none;

    &:hover {
      background-color: $color-main-hover;
    }
  }

  &__content {
    box-sizing: border-box;
    height: 500px;
    align-items: center;
    padding: 40px 16px;
    background-image: url(../../assets/bg.jpg);
    
    background-size: auto 100%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__title {
    font-size: $title-size;
    margin-bottom: 21px;
    font-weight: 400;
  }

  &__text {
    font-size: 16px;
    margin-bottom: 32px;
    line-height: 26px;
  }
}

.buttons-container {
  display: flex;
  gap: 10px;
}

@include for-tabs {
  .header {
    &__container {
      width: 380px;
    }
    &__navbar {
      padding: 0 32px;
    }
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 194px;
    }
  }
}

@include for-desktop {
  .header {
    &__navbar {
      padding: 0 60px;
    }

    &__content {
      background-size: cover;
      height: 650px;
    }
  }
}

@include for-big-desctop {
  .header {
    &__navbar {
      padding: 0;
    }
  }
}