@mixin set-text-overflow {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@mixin for-tabs {
  @media (min-width: 768px) { @content; }
}

@mixin for-desktop {
  @media (min-width: 1024px) { @content; }
}

@mixin for-big-desctop {
  @media (min-width: 2560px) { @content; }
}