@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.card-list {
  text-align: center;

  &__title {
    font-size: $title-size;
    margin-bottom: 50px;
    color: $color-title-black;
    font-weight: 400;
  }

  &__users {
    margin-bottom: 50px;
  }

  &__btn {
    min-width: 100px;
    min-height: 34px;
    background-color: $color-main;
    border: none;
    border-radius: 80px;
    margin-bottom: 140px;
    
    &:hover {
      background-color: $color-main-hover;
    }
    
    &--disabled {
      background-color: $color-grey-disabled;
      &:hover {
        background-color: $color-grey-disabled;
      }
    }
  }
}

@include for-tabs {
  .card-list {
    &__users {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1rem;
      padding: 0 32px;
    }
  }
}

@include for-desktop {
  .card-list {
    &__users {
      grid-template-columns: 1fr 1fr 1fr;
      gap: 1.5rem;
      padding: 0 60px;
    }
  }
}

@include for-big-desctop {
  card-list {
    &__users {
      padding: 0;
    }
  }
}